.group-page {
    
    align-items: center;
    width: 100%;
    padding: 0 !important;
    margin: 0 !important;
    color: #FFFFFF;
    background: rgb(33,17,114);
    background: linear-gradient(180deg, rgba(33,17,114,1) 0%, rgba(18,55,142,1) 100%);
    
}

.group-container{
    min-height: 700px;
}

.group-container .benefits-group{
    text-align: justify;
    font-size: 17px;
    line-height: 28px;
    font-weight: 400;

}


.title-grid {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 40px;
}

.communication-direction {
    margin-top: 30px;
}

.communication-direction a{
    font-size: 16px;
    line-height: 30px;
    color:#FFF;
}

.communication-direction svg{
    width: 20px;
    float: left;
    margin: 4px 4px 0 0;
}

.text-grid {
    align-items: center;
    font-size: 20px;
    margin-bottom: 30px;

}

.image-trilhacerta .image-group{ 
    border-radius: 20px;
    width: 100%;
}